.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    &__logo {
        width: 250px;
        img {
            width: 100%; } }
    &__oficial {
        color: #ffffff;
        font-family: "Museo Sans 500 Medium";
        font-size: 12px;
        font-weight: 500;
        text-align: center;
        span {
            display: block;
            color: #ffffff;
            font-family: "Museo Sans 700 Bold";
            font-size: 18px;
            font-weight: 700;
            text-align: center; } }
    &__phone {
        display: block;
        color: #ffffff;
        font-family: "Museo Sans 700 Bold";
        font-size: 18px;
        font-weight: 700;
        text-align: center;
        &:hover {
            color: #ffffff;
            text-decoration: none; } }
    &__contacts {
        display: flex;
        flex-direction: column;
        align-items: flex-end; } }

